import GameState from "../models/GameState";

export default class MockGameStateService {

    private static _state: GameState = {
        currentMapId: "town",
        currentVertextId: "hut-road", // "scupperden-road",
    };

    public getState(): GameState {
        const stateJson = null; //localStorage.getItem("state");
        const state: GameState = stateJson ? JSON.parse(stateJson) : MockGameStateService._state;

        return state;
    }

    public setState(state: GameState) {
        MockGameStateService._state = state;
        // localStorage.setItem("state", JSON.stringify(state));
    }
}
