import AttributeList from "./AttributeList";
import CalculatedStats from "./CalculatedStats";
import HeroTrait from "./HeroTrait";

export enum Alignment {
    Lawful = 0,
    Neutral = 1,
    Chaotic = 2,
}

export enum HeroClass {
    /*
     * A traditional fighter, guard, barbarian or mercenary for hire
     */
    Warrior = 0,
    /*
     * A scounrel, bandit, robber
     */
    Thief = 1,
    /*
     * A mistrusted user of magic. Usually obviously marked, correpted or tainted
     */
    Sorcerer = 2,
    /*
     * A follower, patron, or somone bound to a god, demon or higher power
     */
    Templar = 3,
}

export enum StatusFlags {
    None = 0x00,
    Paralysed = 0x01,
    Poisoned = 0x04,
    Bleeding = 0x08,
    Terrified = 0x10,
    Charmed = 0x20,
    // Sleeping, Petrified,
}

export enum AbilityFlags {
    None = 0x00,
    Sorcery = 0x01,
    Stealth = 0x02,
    Perception = 0x04,
    PatronBond = 0x08,
    Survival = 0x10,
    Devices = 0x20,
    Acrobatics = 0x40,
}

// TODO: Split hero into core stats & calculated (view) ?
interface Hero {
    // ID of the hero
    id: string;

    // Display name of the hero
    name: string;

    // Origins of the hero
    background: string;

    imageUrl: string;

    // Hero attributes
    attributes: AttributeList;

    // Any special states
    status: StatusFlags;

    stats: CalculatedStats;

    abilities: AbilityFlags;

    alignment: Alignment;

    class: HeroClass;

    // Itesm equpped by the hero
    equippedItems: string[];

    // Hero traits
    traits: HeroTrait[];

    // Hero traits
    itemTraits: HeroTrait[];

    // Hero traits
    temporaryTraits: HeroTrait[];
}

export default Hero;
