import React from "react";
import { Button, makeStyles, Tooltip } from "@material-ui/core";

import ArrowDownwardIcon from "@material-ui/icons/ArrowDownward";
import ArrowUpwardIcon from "@material-ui/icons/ArrowUpward";

import InventorySelector from "./InventorySelector";
import MockPartyService from "../services/MockPartyService";
import Item from "../models/Item";
import MockItemService from "../services/MockItemService";

const useStyles = makeStyles({
    stashImg: {
        width: "25px",
        position: "absolute",
        top: "220px",
        left: "100px",
    },
    inventoryImg: {
        width: "25px",
        position: "absolute",
        top: "420px",
        left: "100px",
    },
    itemsButton: {
        //margin: "auto 10px",
        margin: "0 10px",
    },
    itemsButtonDiv: {
        width: "100%",
        textAlign: "center",
        paddingBottom: "10px",
    },
});

const Stash: React.FunctionComponent = () => {
    const classes = useStyles();

    let [stashSelectedIds, setStashSelectedIds] = React.useState([] as string[]);
    let [inventorySelectedIds, setInventorySelectedIds] = React.useState([] as string[]);

    let [stashItems, setStashItems] = React.useState(null as Item[] | null);
    let [inventoryItems, setInventoryItems] = React.useState(null as Item[] | null);

    const maxItems = 8;

    React.useEffect(() => {
        // Initial load of inventory
        const loadItems = () => {
            // TODO: Try / catch
            const partyService = new MockPartyService();
            const stashItemIds = partyService.getStash();
            const inventoryItemIds = partyService.getInventory();

            const itemService = new MockItemService();
            const newStashItems = itemService.getItems(stashItemIds);
            const newInventoryItems = itemService.getItems(inventoryItemIds);

            setStashItems(newStashItems);
            setInventoryItems(newInventoryItems);
        };

        if (stashItems === null && inventoryItems === null) {
            loadItems();
        }
    });

    const stashSelectionChanged = (ids: string[]) => setStashSelectedIds(ids);
    const inventorySelectionChanged = (ids: string[]) => setInventorySelectedIds(ids);;

    const isStashSelected = (): boolean => stashSelectedIds.length > 0; 
    const isInventorySelected = (): boolean => inventorySelectedIds.length > 0;

    const willStashBeTooBig = (): boolean => (stashItems ? stashItems.length + inventorySelectedIds.length > maxItems : false);
    const willInventoryBeTooBig = (): boolean => (inventoryItems ? inventoryItems.length + stashSelectedIds.length > maxItems : false);

    const takeItem = (ev: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        if (!isStashSelected()) return;

        const service = new MockPartyService();
        for (let i = 0; i < stashSelectedIds.length; i++) {
            service.removeStashItem(stashSelectedIds[i]);
            service.addInventoryItem(stashSelectedIds[i]);
        }

        // Force a reload of items
        setStashSelectedIds([]);
        setStashItems(null);
        setInventoryItems(null);

        // TODO: How to trigger a reload?
    }

    const putItem = (ev: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        if (!isInventorySelected()) return;

        const service = new MockPartyService();
        for (let i = 0; i < inventorySelectedIds.length; i++) {
            service.removeInventoryItem(inventorySelectedIds[i]);
            service.addStashItem(inventorySelectedIds[i]);
        }

        // Force a reload of items
        setInventorySelectedIds([]);
        setStashItems(null);
        setInventoryItems(null);
    };

    if (!stashItems || !inventoryItems)
        return null;

    const stashToolTip = () => {
        if (willInventoryBeTooBig())
            return "Too many items in inventory";

        return isStashSelected() ? "Take Item from the Stash" : "Please select a stash item";
    }

    const inventoryToolTip = () => {
        if (willInventoryBeTooBig()) return "Too many items in stash";

        return isInventorySelected() ? "Put Item in the Stash" : "Please select an inventory item";
    };

    return (
        <div>
            <span>
                <img src="./images/stash.png" alt="stash" className={classes.stashImg} />
                <InventorySelector items={stashItems} multiSelect={true} onSelectionChanged={stashSelectionChanged} />
            </span>
            <div className={classes.itemsButtonDiv}>
                <Tooltip title={stashToolTip()}>
                    <span>
                        <Button
                            className={classes.itemsButton}
                            onClick={takeItem}
                            disabled={willInventoryBeTooBig() || !isStashSelected()}
                            variant="contained"
                            color="primary"
                            startIcon={<ArrowDownwardIcon />}
                        >
                            Take
                        </Button>
                    </span>
                </Tooltip>
                <Tooltip title={inventoryToolTip()}>
                    <span>
                        <Button
                            className={classes.itemsButton}
                            onClick={putItem}
                            disabled={willStashBeTooBig() || !isInventorySelected()}
                            variant="contained"
                            color="primary"
                            startIcon={<ArrowUpwardIcon />}
                        >
                            Put
                        </Button>
                    </span>
                </Tooltip>
            </div>
            <span>
                <img src="./images/inventory.png" alt="inventory" className={classes.inventoryImg} />
                <InventorySelector items={inventoryItems} multiSelect={true} onSelectionChanged={inventorySelectionChanged} />
            </span>
        </div>
    ); 
}

export default Stash;
