import AttributeList from "../models/AttributeList";
import CalculatedStats from "../models/CalculatedStats";
import Hero from "../models/Hero";
import HeroTrait from "../models/HeroTrait";
import Item from "../models/Item";
import Trait, { ModifierType } from "../models/Trait";
import ArrayHelper from "./ArrayHelper";

export default class HeroHelper {
    /*
     * Return all traits (item, core, temporary) on the hero
     */
    public static getAllTraits = (hero: Hero): HeroTrait[] => hero.traits.concat(hero.itemTraits).concat(hero.temporaryTraits);

    /*
     * Add traits from an item to the hero
     */
    public static addItemTraits = (hero: Hero, item: Item): void => {
        for (let i = 0; i < item.traits.length; i++) {
            const itemTrait: Trait = item.traits[i];
            const heroTrait: HeroTrait = {
                id: itemTrait.id,
                name: itemTrait.name,
                type: itemTrait.type,
                behaviour: itemTrait.behaviour,
                from: item.name,
            };
            hero.itemTraits.push(heroTrait);
        }
    };

    /*
     * Remove traits from an item to the hero
     */
    public static removeItemTraits = (hero: Hero, item: Item): void => {
        const matches = hero.itemTraits.filter((t) => t.from === item.name);
        for (let i = 0; i < matches.length; i++) {
            ArrayHelper.remove(hero.itemTraits, matches[i]);
        }
    };

    /*
     * Calculate attribute modifiers
     */
    public static calculateAttributesWithModifiers = (hero: Hero): AttributeList => {
        const allTraits = HeroHelper.getAllTraits(hero);
        let strMod = 0,
            dexMod = 0,
            conMod = 0,
            intMod = 0,
            wisMod = 0,
            chaMod = 0,
            luckMod = 0;

        for (let i = 0; i < allTraits.length; i++) {
            for (let j = 0; j < allTraits[i].behaviour.length; j++) {
                const behaviour = allTraits[i].behaviour[j];
                switch (behaviour.modifierType) {
                    case ModifierType.Strength:
                        strMod += behaviour.modifierValue;
                        break;
                    case ModifierType.Dexterity:
                        dexMod += behaviour.modifierValue;
                        break;
                    case ModifierType.Constitution:
                        conMod += behaviour.modifierValue;
                        break;
                    case ModifierType.Intelligence:
                        intMod += behaviour.modifierValue;
                        break;
                    case ModifierType.Wisdom:
                        wisMod += behaviour.modifierValue;
                        break;
                    case ModifierType.Charisma:
                        chaMod += behaviour.modifierValue;
                        break;
                    case ModifierType.Luck:
                        luckMod += behaviour.modifierValue;
                        break;
                    default:
                        break;
                }
            }
        }

        const attributes = hero.attributes;
        const newAttributeList: AttributeList = {
            strength: { base: attributes.strength.base, modifier: strMod },
            dexterity: { base: attributes.dexterity.base, modifier: dexMod },
            constitution: { base: attributes.constitution.base, modifier: conMod },
            intelligence: { base: attributes.intelligence.base, modifier: intMod },
            wisdom: { base: attributes.wisdom.base, modifier: wisMod },
            charisma: { base: attributes.charisma.base, modifier: chaMod },
            luck: { base: attributes.luck.base, modifier: luckMod },
        };

        return newAttributeList;
    };

    public static calculateStatsWithModifiers = (hero: Hero): CalculatedStats => {
        const allTraits = HeroHelper.getAllTraits(hero);
        let armorMod = 0,
            moveSpeedMod = 0,
            meleeAttackMod = 0,
            meleeAttackSpeedMod = 0,
            meleeDamageDieMod = 0,
            meleeDamageMod = 0,
            rangedAttackMod = 0,
            rangedAttackSpeedMod = 0,
            rangedDamageDieMod = 0,
            rangedDamageMod = 0,
            hpMod = 0,
            hitDieMod = 0,
            spellResistMod = 0;

        for (let i = 0; i < allTraits.length; i++) {
            for (let j = 0; j < allTraits[i].behaviour.length; j++) {
                const behaviour = allTraits[i].behaviour[j];
                switch (behaviour.modifierType) {
                    case ModifierType.Armor:
                        armorMod += behaviour.modifierValue;
                        break;
                    case ModifierType.MoveSpeed:
                        moveSpeedMod += behaviour.modifierValue;
                        break;
                    case ModifierType.MeleeAttack:
                        meleeAttackMod += behaviour.modifierValue;
                        break;
                    case ModifierType.MeleeAttackSpeed:
                        meleeAttackSpeedMod += behaviour.modifierValue;
                        break;
                    case ModifierType.MeleeDamageDie:
                        meleeDamageDieMod += behaviour.modifierValue;
                        break;
                    case ModifierType.MeleeDamage:
                        meleeDamageMod += behaviour.modifierValue;
                        break;
                    case ModifierType.RangedAttack:
                        rangedAttackMod += behaviour.modifierValue;
                        break;
                    case ModifierType.RangedAttackSpeed:
                        rangedAttackSpeedMod += behaviour.modifierValue;
                        break;
                    case ModifierType.RangedDamageDie:
                        rangedDamageDieMod += behaviour.modifierValue;
                        break;
                    case ModifierType.RangedDamage:
                        rangedDamageMod += behaviour.modifierValue;
                        break;
                    case ModifierType.HitPoints:
                        hpMod += behaviour.modifierValue;
                        break;
                    case ModifierType.HitDie:
                        hitDieMod += behaviour.modifierValue;
                        break;
                    case ModifierType.SpellResistance:
                        spellResistMod += behaviour.modifierValue;
                        break;
                    default:
                        break;
                }
            }
        }

        const stats = hero.stats;
        const newStats: CalculatedStats = {
            armour: stats.armour + armorMod,
            attack: stats.attack + meleeAttackMod,
            moveSpeed: stats.moveSpeed + moveSpeedMod,
            attackSpeed: stats.attackSpeed + meleeAttackSpeedMod,
            damageDie: stats.damageDie + meleeDamageDieMod,
            damageBonus: stats.damageBonus + meleeDamageMod,
            maxHitPoints: stats.maxHitPoints + hpMod,
            currentHitPoints: stats.currentHitPoints,
            hitDie: stats.hitDie + hitDieMod,
            spellResistance: stats.spellResistance + spellResistMod,
        };

        return newStats;
    };
}
