import React from "react";
import { Avatar, Grid, List, ListItem, ListItemAvatar, ListItemText, makeStyles } from "@material-ui/core";
import FavoriteIcon from "@material-ui/icons/Favorite";
import SecurityIcon from "@material-ui/icons/Security";
import FastForwardIcon from "@material-ui/icons/FastForward";
import LocationSearchingIcon from "@material-ui/icons/LocationSearching";

import CalculatedStats from "../models/CalculatedStats";


const useStyles = makeStyles({
    root: {
        width: "100%",
        maxWidth: 360,
        //backgroundColor: theme.palette.background.paper,
    },
    statisticsBlock: {
        padding: "40px 0",
    },
});

type CalculatedStatisticsBlockProps = {
    statistics: CalculatedStats;
};

const CalculatedStatisticsBlock: React.FunctionComponent<CalculatedStatisticsBlockProps> = (props: CalculatedStatisticsBlockProps) => {
    const classes = useStyles();

    const { statistics } = props;

    return (
        <div className={classes.statisticsBlock}>
            <Grid container>
                <Grid item xs={1} md={1}></Grid>
                <Grid item xs={10} md={5}>
                    <List className={classes.root}>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar>
                                    <FavoriteIcon />
                                </Avatar>
                            </ListItemAvatar>
                            <ListItemText
                                primary={`Hit Points ${statistics.currentHitPoints} / ${statistics.currentHitPoints}`}
                                secondary={`Hit Die D${statistics.hitDie}`}
                            />
                        </ListItem>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar>
                                    <SecurityIcon />
                                </Avatar>
                            </ListItemAvatar>
                            <ListItemText
                                primary={`Armor Class ${statistics.armour}`}
                                secondary={`Spell Resistance ${statistics.spellResistance}`}
                            />
                        </ListItem>
                    </List>
                </Grid>
                <Grid item xs={10} md={5}>
                    <List className={classes.root}>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar>
                                    <FastForwardIcon />
                                </Avatar>
                            </ListItemAvatar>
                            <ListItemText
                                primary={`Attack Speed ${statistics.attackSpeed}`}
                                secondary={`Move Speed ${statistics.moveSpeed}`}
                            />
                        </ListItem>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar>
                                    <LocationSearchingIcon />
                                </Avatar>
                            </ListItemAvatar>
                            <ListItemText
                                primary={`Attack +${statistics.attack}`}
                                secondary={`Damage D${statistics.damageDie} + ${statistics.damageBonus}`}
                            />
                        </ListItem>
                    </List>
                </Grid>
                <Grid item xs={1} md={1}></Grid>
            </Grid>
        </div>
    );
}

export default CalculatedStatisticsBlock;
