import React from "react";
import { Button, makeStyles, Tooltip } from "@material-ui/core";

import ArrowUpwardIcon from "@material-ui/icons/ArrowUpward";

import InventorySelector from "./InventorySelector";
import Item from "../models/Item";

const useStyles = makeStyles({
    itemsButton: {
        //margin: "auto 10px",
        margin: "0 10px",
    },
    itemsButtonDiv: {
        width: "100%",
        textAlign: "center",
        paddingBottom: "10px",
    },
});

type PartyInventoryProps = {
    items: Item[];
    onEquipItem: (itemId: string) => void;
};

const PartyInventory: React.FunctionComponent<PartyInventoryProps> = (props: PartyInventoryProps) => {
    const classes = useStyles();

    const { items, onEquipItem } = props;

    let [inventorySelectedIds, setInventorySelectedIds] = React.useState([] as string[]);

    const inventorySelectionChanged = (ids: string[]) => setInventorySelectedIds(ids);

    const isInventorySelected = (): boolean => inventorySelectedIds.length > 0;
    const isItemEquipable = (): boolean => {
        if (!isInventorySelected) return false;

        const match = items.find((item) => item.id === inventorySelectedIds[0]);
        if (!match) return false; // Error?

        return match.itemType >= 10;
    };

    const equipItem = () => {
        if (!isInventorySelected()) return;

        if (onEquipItem) onEquipItem(inventorySelectedIds[0]);
    };

    const inventoryToolTip = () => {
        if (!isInventorySelected()) return "Please select an inventory item";

        return isItemEquipable() ? "Equip the item" : "Please select an equipable item";
    };

    return (
        <div>
            <span>
                <InventorySelector items={items} onSelectionChanged={inventorySelectionChanged} />
            </span>
            <div className={classes.itemsButtonDiv}>
                <Tooltip title={inventoryToolTip()}>
                    <span>
                        <Button
                            className={classes.itemsButton}
                            onClick={equipItem}
                            disabled={!isInventorySelected()}
                            variant="contained"
                            color="primary"
                            startIcon={<ArrowUpwardIcon />}
                        >
                            Equip
                        </Button>
                    </span>
                </Tooltip>
            </div>
        </div>
    );
};

export default PartyInventory;
