import Leaflet from "leaflet";

import MapData from "../models/MapData";
import MapVertex from "../models/MapVertex";
import IMapService from "./IMapService";

import BlackTempleMap from "./maps/blacktemple.json";
import TownMap from "./maps/town.json";

export default class MockMapService implements IMapService {
    public getMap(id: string): MapData | null {

        let json: any = null;

        switch (id) {
            case "black_temple":
                json = BlackTempleMap;
                break;
            case "town":
                json = TownMap;
                break;
            default:
                json = BlackTempleMap;
                break;
        }
        
        const map = {
            id: json.id,
            imageUrl: json.imageUrl,
            vertices: json.vertices.map(
                (v: any) => {
                    return {
                        id: v.id,
                        position: Leaflet.latLng(v.position[0], v.position[1]),
                        clickRadius: v.clickRadius,
                        encounterId: v.encounterId,
                        marker: v.marker,
                        exitId: v.exitId,
                    } as MapVertex;
                }
            ),
            edges: json.edges,
        } as MapData;

        return map;
    }
}
