import { AbilityFlags, Alignment, HeroClass } from "../models/Hero";
import { ItemType } from "../models/Item";
import { ModifierType } from "../models/Trait";
import Resources from "../resources.en.json";

export default class StringConverters {
    private static resourceMap: any = null;

    static getResources(): any {
        if (!StringConverters.resourceMap) {
            // TODO: This is silly
            StringConverters.resourceMap = JSON.parse(JSON.stringify(Resources));
        }

        return StringConverters.resourceMap;
    }

    static classToString = (heroClass: HeroClass): string => {
        const resources = StringConverters.getResources();

        const key = "HeroClass_" + HeroClass[heroClass];
        return key in resources ? resources[key] : "Unsure";
    };

    static alignmentToString = (alignment: Alignment): string => {
        const resources = StringConverters.getResources();

        const key = "Alignment_" + Alignment[alignment];
        return key in resources ? resources[key] : "Unsure";
    };

    static modifierToString = (modifierType: ModifierType): string => {
        const resources = StringConverters.getResources();

        const key = "ModifierType_" + ModifierType[modifierType];
        return key in resources ? resources[key] : "Unsure";
    };

    static itemTypeText = (itemType: ItemType): string => {
        const resources = StringConverters.getResources();

        const key = "ItemType_" + ItemType[itemType];
        return key in resources ? resources[key] : "Unsure";
    };

    static abilitiesToString = (abilities: AbilityFlags): string => {
        if (abilities === AbilityFlags.None) return "None";

        const output: string[] = [];

        if (abilities & AbilityFlags.Acrobatics) output.push("Acrobatics");
        if (abilities & AbilityFlags.Devices) output.push("Disable/Modify Devices");
        if (abilities & AbilityFlags.PatronBond) output.push("Patron Bond");
        if (abilities & AbilityFlags.Perception) output.push("Perception");
        if (abilities & AbilityFlags.Sorcery) output.push("Sorcery");
        if (abilities & AbilityFlags.Stealth) output.push("Stealth");
        if (abilities & AbilityFlags.Survival) output.push("Survival");

        return output.join(", ");
    };
}
