import { makeStyles } from "@material-ui/core";
import React from "react";

import Hero from "../models/Hero";
import MockPartyService from "../services/MockPartyService";
import LinkTo from "./LinkTo";

const useStyles = makeStyles({
    appHeader: {
        backgroundColor: "#282c34",
        "-webkit-app-region": "drag",
    },
    headerSpan: {
        width: "100%",
        display: "flex",
    },
    headerParty: {
        position: "absolute",
        zIndex: 1000,
        right: "10px",
        top: "2vh",
        "-webkit-app-region": "none",
    },
    headerImg: {
        width: "7vh",
        height: "7vh",
        border: "solid",
        borderRadius: "50%",
        borderWidth: "10px",
        borderColor: "#282c34",
        margin: "4px",
    },
    appLogo: {
        height: "5vh",
        pointerEvents: "none",
        padding: "20px",
        "-webkit-app-region": "none",
    },
});

type HeaderProps = {};


const Header: React.FunctionComponent<HeaderProps> = (props: HeaderProps): JSX.Element => {
    const classes = useStyles();

    let [party, setParty] = React.useState(null as Hero[] | null); // The map graph data

    React.useEffect(() => {
        // Initial load of party
        const loadParty = () => {
            const heroService = new MockPartyService();
            const newParty = heroService.getParty();
            if (!newParty) {
                console.error(`No party data`);
                return;
            }

            setParty(newParty);
        };

        if (party == null) {
            loadParty();
        }
    });

    const renderParty = () => {
        if (!party) return null;

        return party.map((hero) => {
            return (
                <LinkTo key={hero.id} url={`${process.env.PUBLIC_URL}/profile/${hero.id}`}>
                    <img src={hero.imageUrl} className={classes.headerImg} alt="profile" />
                </LinkTo>
            );
        });
    };

    return (
        <header className={classes.appHeader}>
            <span className={classes.headerSpan}>
                <LinkTo url={`${process.env.PUBLIC_URL}/menu`}>
                    <img src="./volcano.svg" className={classes.appLogo} alt="logo" />
                </LinkTo>
                <img className="title shadow" alt="Tales from the Pit" src="title.svg" />
                <span className={classes.headerParty}>{renderParty()}</span>
            </span>
        </header>
    );
}

export default Header;
