import React from "react";
import { Button, makeStyles, Tooltip } from "@material-ui/core";

import HeroSelector from "./HeroSelector";
import MockHeroService from "../services/MockHeroService";

const useStyles = makeStyles({
    partyButton: {
        //margin: "auto 10px",
    },
    partyButtonDiv: {
        width: "100%",
        textAlign: "center",
    }
});


const HeroDismiss: React.FunctionComponent = (): JSX.Element => {
    const classes = useStyles();

    let [heroId, setHeroId] = React.useState("");

    const selectionChanged = (ids: string[]) => {

        if (ids.length > 0)
            setHeroId(ids[0]);
    };

    const isValid = () => heroId.length > 0; 

    const dismissHero = (ev: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        if (!isValid()) return;

        const service = new MockHeroService();
        
        // TODO: How to trigger a header reload?
        // TODO: Maybe a shared callback?
    }

    return (
        <div>
            <HeroSelector onSelectionChanged={selectionChanged} chooseAll={true} />
            {/* TODO: Tooltip not working when button disabled? */}
            <Tooltip title={isValid() ? "Dismiss a hero" : "Please select a hero"}>
                <div className={classes.partyButtonDiv}>
                    <Button className={classes.partyButton} onClick={dismissHero} disabled={!isValid()} variant="contained" color="primary">
                        Dismiss
                    </Button>
                </div>
            </Tooltip>
        </div>
    ); 
}

export default HeroDismiss;
