import React from "react";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import AttributeList from "../models/AttributeList";
import Attribute from "../models/Attribute";

const StyledTableCell = withStyles((theme) => ({
    head: {
        fontFamily: "Martel",
        backgroundColor: theme.palette.common.black,
        color: theme.palette.common.white,
    },
    body: {
        fontFamily: "Martel",
        fontSize: 14,
    },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
    root: {
        "&:nth-of-type(odd)": {
            backgroundColor: theme.palette.action.hover,
        },
    },
}))(TableRow);

const useStyles = makeStyles({
    table: {
        // minWidth: 700,
    },
});

type StatisticsBlockProps = {
    statistics: AttributeList
};

const StatisticsBlock: React.FunctionComponent<StatisticsBlockProps> = (props: StatisticsBlockProps) => {
    const classes = useStyles();

    const { statistics } = props;

    const createData = (name: string, base: number, modifier: number, total: number) => {
        return { name, base, modifier, total };
    }

    const attributeTotal = (attribute: Attribute) => attribute.base + attribute.modifier;

    const rows = [
        createData("Strength", statistics.strength.base, statistics.strength.modifier, attributeTotal(statistics.strength)),
        createData("Dexterity", statistics.dexterity.base, statistics.dexterity.modifier, attributeTotal(statistics.dexterity)),
        createData("Consitution", statistics.constitution.base, statistics.constitution.modifier, attributeTotal(statistics.constitution)),
        createData("Intelligence", statistics.intelligence.base, statistics.intelligence.modifier, attributeTotal(statistics.intelligence)),
        createData("Wisdom", statistics.wisdom.base, statistics.wisdom.modifier, attributeTotal(statistics.wisdom)),
        createData("Charisma", statistics.charisma.base, statistics.charisma.modifier, attributeTotal(statistics.charisma)),
        createData("Luck", statistics.strength.base, statistics.strength.modifier, attributeTotal(statistics.strength)),
    ];

    return (
        <TableContainer component={Paper}>
            <Table className={classes.table} aria-label="customized table">
                <TableHead>
                    <TableRow>
                        <StyledTableCell>Attribute</StyledTableCell>
                        <StyledTableCell align="right">Base</StyledTableCell>
                        <StyledTableCell align="right">Modifier</StyledTableCell>
                        <StyledTableCell align="right">Total</StyledTableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {rows.map((row) => (
                        <StyledTableRow key={row.name}>
                            <StyledTableCell component="th" scope="row">
                                {row.name}
                            </StyledTableCell>
                            <StyledTableCell align="right">{row.base}</StyledTableCell>
                            <StyledTableCell align="right">{row.modifier}</StyledTableCell>
                            <StyledTableCell align="right">{row.total}</StyledTableCell>
                        </StyledTableRow>
                    ))}
                </TableBody>
            </Table>
        </TableContainer>
    );
}

export default StatisticsBlock;