
export enum TraitType {
    Uncategorised = 0,
    Mental = 1,
    Mystical = 2,
    Physical = 3,
    Item = 4,
    // MentalPhobia = 100,
    // MysticalCurse = 200,
};

export enum ModifierType {
    None = 0,
    Strength = 10,
    Dexterity = 11,
    Constitution = 12,
    Intelligence = 13,
    Wisdom = 14,
    Charisma = 15,
    Luck = 16,
    HitPoints = 20,
    HitDie = 21,
    Armor = 22,
    SpellResistance = 23,
    MoveSpeed = 24,
    MeleeAttackSpeed = 25,
    MeleeAttack = 26,
    MeleeDamageDie = 27,
    MeleeDamage = 28,
    RangedAttackSpeed = 29,
    RangedAttack = 30,
    RangedDamageDie = 31,
    RangedDamage = 32,
    MeleeChanceForParalysis = 50,
    MeleeChanceForPoisoned = 51,
    MeleeChanceForBleeding = 52,
    MeleeChanceForTerrified = 53,
    MeleeChanceForCharmed = 54,
    RangedChanceForParalysis = 60,
    RangedChanceForPoisoned = 61,
    RangedChanceForBleeding = 62,
    RangedChanceForTerrified = 63,
    RangedChanceForCharmed = 64,
    AddAbility = 100,
    RemoveAbility = 101, // Items can prevent abilities being used (e.g. Armor prevents Sorcery)
    AbilitySorcery = 110,
    AbilityStealth = 111,
    AbilityPerception = 112,
    AbilityPatronBond = 113,
    AbilitySurvival = 114,
    AbilityDevices = 115,
    AbilityAcrobatics = 116,
};

export interface TraitEffect {

    modifierType: ModifierType;

    modifierValue: number;
}

export default interface Trait {
    // ID for the trait
    id: string;

    // A name for the trait
    name: string;

    // The type of trait
    type: TraitType;

    // What does it do
    behaviour: TraitEffect[];
}
