import React from 'react';
import { useHistory } from "react-router-dom";
import { Card, CardHeader, CardContent, Grid, makeStyles, Typography } from "@material-ui/core";

import LinkTo from '../components/LinkTo';
import KeyMaps from '../components/KeyMaps';

const useStyles = makeStyles({
    root: {
        backgroundColor: "#282c34",
        height: "100vh",
    },
    close: {
        filter: "invert(60%)",
    },
    container: {
        top: "1vh",
        position: "relative",
    },
    titleContainer: {
        padding: "10vh",
    },
    appLogo: {
        height: "10vh",
        pointerEvents: "none",
        padding: "20px",
    },
    chooseText: {
        color: "white",
    },
    card: {
        position: "relative",
        margin: "40px",
        cursor: "pointer",
        backgroundColor: "lightsteelblue",
        "&:hover": {
            top: "-5px",
            right: "5px",
            boxShadow: "10px 10px",
            backgroundColor: "ghostwhite",
        },
    },
});

const SavesMenu: React.FunctionComponent = () => {
    const classes = useStyles();

    let history = useHistory();

    const isElectron = () => {
        // Renderer process
        if (typeof window !== "undefined" && typeof window.process === "object" && window.process.type === "renderer") {
            return true;
        }

        // Main process
        if (typeof process !== "undefined" && typeof process.versions === "object" && !!process.versions.electron) {
            return true;
        }

        // Detect the user agent when the `nodeIntegration` option is set to true
        if (typeof navigator === "object" && typeof navigator.userAgent === "string" && navigator.userAgent.indexOf("Electron") >= 0) {
            return true;
        }

        return false;
    };

    const onExit = () => {
        if (isElectron()) {
            const ipcRenderer = window.require("electron").ipcRenderer;
            ipcRenderer.send("close-app");
        } else {
            history.push("/");
        }
    };

    return (
        <KeyMaps backUrl="/menu">
            <div className={classes.root}>
                <LinkTo url={`${process.env.PUBLIC_URL}/menu`} top="20px" left="20px" width="40px">
                    <img src="images/close.svg" className={classes.close} alt="close" />
                </LinkTo>
                <div className={classes.titleContainer}>
                    <img src="./volcano.svg" className={classes.appLogo} alt="logo" />
                    <img className="title-large shadow" alt="Tales from the Pit" src="title.svg" />
                </div>
                <Typography variant="h4" className={classes.chooseText}>Choose a save slot</Typography>
                <Grid container className={classes.container}>
                    <Grid item xs={3}></Grid>
                    <Grid item xs={6}>
                        <Card className={classes.card}>
                            <CardHeader title={<Typography variant="h2">Slot A</Typography>} />
                            <CardContent>Empty</CardContent>
                        </Card>
                        <Card className={classes.card}>
                            <CardHeader title={<Typography variant="h2">Slot B</Typography>} />
                            <CardContent>Empty</CardContent>
                        </Card>
                        <Card className={classes.card}>
                            <CardHeader title={<Typography variant="h2">Slot C</Typography>} />
                            <CardContent>Empty</CardContent>
                        </Card>
                    </Grid>
                    <Grid item xs={3}></Grid>
                </Grid>
            </div>
        </KeyMaps>
    );
};

export default SavesMenu;
