import "./App.scss";

import React from "react";
import { HashRouter, Route, Switch } from "react-router-dom";
import { ThemeProvider } from "@material-ui/core";

import PitTheme from "./PitTheme";

// Pages
import MainMenu from "./pages/MainMenu";
import MainLayout from "./MainLayout";
import SavesMenu from "./pages/SavesMenu";

const App : React.FunctionComponent = () => {

    return (
        <div className="app">
            <ThemeProvider theme={PitTheme}>
                <HashRouter>
                    <Switch>
                        <Route path={`${process.env.PUBLIC_URL}/menu`} component={MainMenu} />
                        <Route path={`${process.env.PUBLIC_URL}/saves`} component={SavesMenu} />
                        <Route path={`${process.env.PUBLIC_URL}/`} component={MainLayout} />
                    </Switch>
                </HashRouter>
            </ThemeProvider>
        </div>
    );
}

export default App;
