import React from "react";
import { Button, makeStyles, Tooltip } from "@material-ui/core";

import HeroSelector from "./HeroSelector";
import MockPartyService from "../services/MockPartyService";

const useStyles = makeStyles({
    partyButton: {
        //margin: "auto 10px",
    },
    partyButtonDiv: {
        width: "100%",
        textAlign: "center",
    }
});

const PartySelection: React.FunctionComponent = () => {
    const classes = useStyles();

    let [heroIds, setHeroIds] = React.useState([] as string[]);

    const selectionChanged = (ids: string[]) => {

        setHeroIds(ids);
    };

    const isValid = () => heroIds.length === 4; 

    const changeParty = (ev: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        if (!isValid()) return;

        const service = new MockPartyService();
        service.setPartyIds(heroIds);
        // TODO: How to trigger a header reload?
        // TODO: Maybe a shared callback?
    }

    return (
        <div>
            <HeroSelector onSelectionChanged={selectionChanged} chooseAll={true} multiSelect={true} />
            {/* TODO: Tooltip not working when button disabled? */}
            <Tooltip title={isValid() ? "Apply party changes" : "Please select four heroes"}>
                <div className={classes.partyButtonDiv}>
                    <Button className={classes.partyButton} onClick={changeParty} disabled={!isValid()} variant="contained" color="primary">
                        Apply
                    </Button>
                </div>
            </Tooltip>
        </div>
    ); 
}

export default PartySelection;
