import React from "react";
import { Grid, Tooltip, makeStyles } from "@material-ui/core";

import Hero from "../models/Hero";
import MockHeroService from "../services/MockHeroService";
import MockPartyService from "../services/MockPartyService";

const useStyles = makeStyles({
    container: {
        position: "relative",
        height: "250px",
    },
    party: {
        padding: "30px",
        position: "absolute",
    },
    hero: {
        width: "70px",
        height: "70px",
        border: "solid",
        borderRadius: "50%",
        borderWidth: "10px",
        borderColor: "lightgrey",
        margin: "4px",
        cursor: "pointer",
    },
    heroSelected: {
        width: "70px",
        height: "70px",
        border: "solid",
        borderRadius: "50%",
        borderWidth: "10px",
        borderColor: "mediumaquamarine",
        margin: "4px",
        cursor: "pointer",
    },
});

type HeroSelectorProps = {
    onSelectionChanged?: (ids: string[]) => void;
    // encounterId: string | null;
    chooseAll?: boolean;
    multiSelect?: boolean;
};

const HeroSelector: React.FunctionComponent<HeroSelectorProps> = (props: HeroSelectorProps) => {
    const classes = useStyles();
    const { onSelectionChanged, multiSelect } = props;

    let [heroes, setHeroes] = React.useState(null as Hero[] | null); // The map graph data
    let [selected, setSelected] = React.useState([] as boolean[]);

    React.useEffect(() => {
        // Initial load of party
        const loadHeroes = () => {
            const heroService = new MockHeroService();
            const partyService = new MockPartyService();
            const newParty = props.chooseAll ? heroService.getAllAvailable() : partyService.getParty();
            if (!newParty) {
                console.error(`No party data`);
                return;
            }

            setHeroes(newParty);
        };

        if (heroes == null) {
            loadHeroes();
        }
    });

    const onSelect = (ev: React.MouseEvent<HTMLImageElement, MouseEvent>) => {
        const id = Number.parseInt(ev.currentTarget.id);

        const newSelected = multiSelect ? selected.map((t) => t) : [];

        newSelected[id] = !newSelected[id];
        setSelected(newSelected);

        if (!heroes) return;

        const selectedHeroIds: string[] = [];
        for (let i = 0; i < 8; i++) {
            if (newSelected[i]) selectedHeroIds.push(heroes[i].id);
        }

        if (onSelectionChanged) onSelectionChanged(selectedHeroIds);
    };

    const renderParty = () => {
        if (!heroes) return null;

        let index = 0;

        return heroes.map((hero) => {
            const imgClass = selected[index] ? `${classes.heroSelected}` : `${classes.hero}`;
            const output = (
                <Grid item xs={3}>
                    <Tooltip title={hero.name} key={index}>
                        <img src={hero.imageUrl} id={index.toString()} onClick={onSelect} className={imgClass} alt="profile" />
                    </Tooltip>
                </Grid>
            );
            index++;
            return output;
        });
    };

    return (
        <div className={classes.container}>
            <div className={classes.party}>
                <Grid container>{renderParty()}</Grid>
            </div>
        </div>
    );
}

export default HeroSelector;
