import "./App.scss";

import React from "react";
import { HashRouter, Route, Switch } from "react-router-dom";

import Header from "./components/Header";

// Pages
import OverheadMap from "./pages/OverheadMap";
import HeroProfile from "./pages/HeroProfile";
import EncounterMap from "./pages/EncounterMap";
import KeyMaps from "./components/KeyMaps";

const MainLayout: React.FunctionComponent = () => {
    return (
        <div>
            <KeyMaps backUrl="/menu">
                <Header />
            </KeyMaps>
            <HashRouter>
                <Switch>
                    <Route
                        path={`${process.env.PUBLIC_URL}/profile/:id`}
                        component={() => {
                            return <HeroProfile />;
                        }}
                    />
                    <Route path={`${process.env.PUBLIC_URL}/encounter`} component={EncounterMap} />
                    <Route path={`${process.env.PUBLIC_URL}/:id`} component={OverheadMap} />
                    <Route path={`${process.env.PUBLIC_URL}/`} component={OverheadMap} />
                </Switch>
            </HashRouter>
        </div>
    );
}

export default MainLayout;
