import React, { useEffect, useState } from 'react';

// UI Elements
import { CRS, Icon, latLng, LatLngBounds } from 'leaflet';
import { Map, ImageOverlay, Polygon } from "react-leaflet";
import DriftMarker from '../components/DriftMarker';
import EncounterDialog from "../components/EncounterDialog";

// Services
import MockDetailedEncounterService from "../services/MockDetailedEncounterService";

// Pathfinding imports
import Queue from '../models/Queue';
import { Graph, Node } from "ngraph.graph";

type EncounterMapProps = {
    // None
};

export function EncounterMap(props: EncounterMapProps) {
    // const pos = { lat: -51.505, lng: -0.09 } as any;
    const initialPos = latLng([0, 0]);
    const initialNode = "common-room";

    let [pos, setPos] = useState(initialPos); // The current (target) position of the player marker
    let [currentVertex, setCurrentVertex] = useState(initialNode); // The current vertex that the player marker is at
    let [vertexQueue] = useState(new Queue<any>()); // A queue of points to visit during path-finding
    let [encounterId, setEncounterId] = useState(null as string | null);

    // Build verticies from the graph
    // TODO: Is there an easier method to use?
    const getVertices = (graph: Graph<any, any> | null): Node<any>[] => {
        if (!graph) return [];

        var vertices: Node<any>[] = [];
        graph.forEachNode((n: Node<any>) => {
            vertices.push(n);
        });

        return vertices;
    };

    // // Get the vertex nearest to the clicked point
    // const findNearestVertex = (graph: Graph<any, any> | null, pos: LatLng): any => {
    //     const vertices = getVertices(graph);

    //     for (let i = 0; i < vertices.length; i++) {
    //         const vertex = vertices[i];

    //         const dist = distance(pos, vertex.data["position"]);
    //         const isInRadius = dist < vertex.data["clickRadius"];

    //         if (isInRadius) {
    //             return vertex;
    //         }
    //         // console.log(`Distance to ${vertex.id} is (${dist} < ${vertex.clickRadius}) = ${isInRadius}`);
    //     }

    //     return null;
    // };

    // Fire when marker move has finished
    const handleMoveEnd = (marker: any) => {
        console.log("Move ended");

        var targetVertex = vertexQueue.pop();
        if (targetVertex) {
            console.log(`Next vertex ${targetVertex.id}`);

            setPos(targetVertex.data["position"]);
            setCurrentVertex(targetVertex.id);

            const targetEncounterId = targetVertex.data["encounterId"];
            if (targetEncounterId) {
                // console.log(`Set encounter ${targetEncounterId}`);
                setEncounterId(targetEncounterId);
            }
        }
    };

    // Fire when user clicks on the map
    const handleClick = (e: any) => {
        //if (!graph) return; // Map not loaded

        console.log("clicked at: " + e.latlng);
    };

    const handleEncounterClose = (moveTo: string | undefined) => {
        console.log("Close encounter");
        setEncounterId(null);
    };

    useEffect(() => {
        // Initial load of map data
        // const loadMap = () => {
        //     const service = new MockMapService();
        //     const data = service.getMap("temple");
        //     if (!data) {
        //         console.error(`No map data`);
        //         return;
        //     }
        //     console.log(`Found ${data?.vertices.length} vertices`);
        //     const graph = GraphBuilder.Build(data);
        //     setGraph(graph);
        // };
        // if (graph == null) {
        //     loadMap();
        // }
    });

    //const bounds = new LatLngBounds(latLng([0.0, 0.0]), latLng([0.009634, 0.009642]));
    const bounds = new LatLngBounds(latLng([0.0, 0.0]), latLng([1609, 2143]));
    //const maxBounds = new LatLngBounds(latLng([-0.0002, 0.0]), latLng([0.01025, 0.012]));

    // maxBounds={bounds}
    return (
        <Map
            crs={CRS.Simple}
            zoom={3}
            minZoom={-5}
            maxZoom={20}
            bounds={bounds}
            maxBounds={bounds}
            scrollWheelZoom={false}
            onClick={handleClick}
            zoomControl={true}
        >
            <EncounterDialog onClose={handleEncounterClose} encounterId={encounterId} />
            {/* <TileLayer url="tiles/Tavern/row-{x}-col-{y}.png" bounds={bounds} /> */}
            <ImageOverlay url="maps/tavern.png" bounds={bounds} />
        </Map>
    );
}

export default EncounterMap;
