import { Choice } from "inkjs/engine/Choice";
import { Story } from "inkjs/engine/Story";
import { StringValue } from "inkjs/engine/Value";
import InkStoryEncounter from "../models/InkStoryEncounter";
import InkStoryChoice from "../models/InkStoryChoice";

import TheInterceptContent from "./encounters/TheIntercept.json";
import commonRoomContent from "./encounters/common-room.ink.json";
import chasmContent from "./encounters/chasm.ink.json";
import homeContent from "./encounters/home.ink.json";
import nineBellsContent from "./encounters/nine-bells.ink.json";
import brawlContent from "./encounters/brawl.ink.json";

// A mock story service
// Story state will be kept on the server
export default class MockStoryEncounterService {
    private currentStory: Story | null = null;
    private currentId: string | null = null;
    private currentTitle: string = "";

    public startEncounter(id: string): InkStoryEncounter {
        switch (id) {
            case "hoard":
                this.currentTitle = "Treasure !";
                this.currentStory = new Story(TheInterceptContent);
                //paragraphText: "You stumble tired and exhaused into a room stuffed to the ceiling with well-crafted wooden coffers.",
                break;
            case "wet-room":
                this.currentTitle = "The Intercept !";
                this.currentStory = new Story(TheInterceptContent);
                break;
            case "common-room":
                this.currentTitle = "Tracking the Yellow-Bellied Crook";
                this.currentStory = new Story(commonRoomContent);
                break;
            case "chasm":
                this.currentTitle = "Across the Chasm";
                this.currentStory = new Story(chasmContent);
                break;
            case "warrens":
                this.currentTitle = "Rats !";
                this.currentStory = new Story(TheInterceptContent);
                break;
            case "home":
                this.currentTitle = "Scupperden";
                this.currentStory = new Story(homeContent);
                break;
            case "nine-bells":
                this.currentTitle = "The Cathedral";
                this.currentStory = new Story(nineBellsContent);
                break;
            case "brawl":
                this.currentTitle = "Thugs!";
                this.currentStory = new Story(brawlContent);
                break;
            default:
                return MockStoryEncounterService.createEmptyStory();
        }
        this.currentId = id;

        return this.continue();
    }

    public chooseChoice(choiceId: number): InkStoryEncounter {
        if (!this.currentStory || !this.currentId) return MockStoryEncounterService.createEmptyStory();
        this.currentStory.ChooseChoiceIndex(choiceId);

        return this.continue();
    }

    public continue(): InkStoryEncounter {
        if (!this.currentStory || !this.currentId) return MockStoryEncounterService.createEmptyStory();

        if (!this.currentStory.canContinue) {
            const moveTo = this.currentStory.variablesState.GetVariableWithName("moveTo") as StringValue;
            const detailedEncounter = this.currentStory.variablesState.GetVariableWithName("detailedEncounter") as StringValue;
            return {
                id: this.currentId,
                title: this.currentTitle,
                paragraphText: "",
                choices: [],
                isFinished: true,
                finishData: {
                    moveTo: moveTo?.value,
                    detailedEncounter: detailedEncounter?.value,
                },
            };
        }

        const paragraphText = this.currentStory.Continue();
        if (!paragraphText) return MockStoryEncounterService.createEmptyStory();

        const encounterChoices: InkStoryChoice[] = this.currentStory.currentChoices.map(
            (c: Choice) => {
                return {
                    index: c.index,
                    text: c.text,
                } as InkStoryChoice
            }
        );

        const encounter: InkStoryEncounter = {
            id: this.currentId,
            title: this.currentTitle,
            paragraphText: paragraphText,
            choices: encounterChoices,
            isFinished: false,
        };

        return encounter;
    }

    private static createEmptyStory(): InkStoryEncounter {
        return {
            id: "",
            title: "",
            paragraphText: "",
            choices: [],
            isFinished: true,
        };
    }
}
