import { LatLng } from "leaflet";
import { Graph, Node } from "ngraph.graph";
import CustomMarker from "./CustomMarker";

// Calculates the distance between two points
export const distance = (x: LatLng, y: LatLng): number => {
    const latDist = x.lat - y.lat;
    const lngDist = x.lng - y.lng;
    return Math.sqrt(latDist * latDist + lngDist * lngDist);
};

export class MapGraph {
    constructor(graph: Graph<NodeData, VertexData>) {
        this._graph = graph;
    }

    private _graph: Graph<NodeData, VertexData>;

    public getNGraph = (): Graph<NodeData, VertexData> => this._graph;

    // Build verticies from the graph
    // TODO: Is there an easier method to use?
    public getVertices = (): Node<NodeData>[] => {
        var vertices: Node<NodeData>[] = [];
        this._graph.forEachNode((n: Node<NodeData>) => {
            vertices.push(n);
        });

        return vertices;
    };

    public findVertex = (nodeId: string): Node<NodeData> | undefined => this._graph?.getNode(nodeId);

    // Get the vertex nearest to the clicked point
    public findNearestVertex = (pos: LatLng): Node<NodeData> | null => {
        const vertices = this.getVertices();

        for (let i = 0; i < vertices.length; i++) {
            const vertex = vertices[i];

            const dist = distance(pos, vertex.data.position);
            const isInRadius = dist < vertex.data.clickRadius;

            if (isInRadius) {
                console.log("match");
                return vertex;
            }
            // console.log(`Distance to ${vertex.id} is (${dist} < ${vertex.data.clickRadius}) = ${isInRadius}`);
        }

        return null;
    };
}

export interface NodeData {
    position: LatLng;
    clickRadius: number;
    encounterId?: string;
    exitId?: string;
    marker?: CustomMarker;
}

export interface VertexData {
    weight: number;
}