import React from "react";

import Leaflet from "leaflet";
import { Marker, Tooltip } from "react-leaflet";

import { makeStyles } from "@material-ui/core";
import GpsFixedIcon from "@material-ui/icons/GpsFixed";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import HomeIcon from "@material-ui/icons/Home";

import { Node } from "ngraph.graph";
import { NodeData } from "../models/MapGraph";

const useStyles = makeStyles({
    tooltip: {
        padding: "3px 8px 3px 3px",
        height: "32px",
        display: "inline-flex",
        fontSize: "0.8125rem",
        // //transition: background-color 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
        alignItems: "center",
        fontFamily: '"Roboto", "Helvetica", "Arial", "sans-serif"',
        whiteSpace: "nowrap",
        borderRadius: "10px",
        verticalAlign: "middle",
        justifyContent: "center",
    },
    icon: {
        color: "#616161",
        marginLeft: "3px",
        marginRight: "5px",
        width: "1em",
        height: "1em",
        display: "inline-block",
        fontSize: "1.5rem",
        //transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
        flexShrink: 0,
        userSelect: "none",
    },
    text: {
        overflow: "hidden",
        whiteSpace: "nowrap",
    },
});

type VertexMarkerProps = {
    vertex: Node<NodeData>;
    onHover: (id: string) => void;
    onHoverEnd: (id: string) => void;
    onClick: (id: string) => void;
};

const VertexMarker: React.FunctionComponent<VertexMarkerProps> = (props: VertexMarkerProps) => {

    const { vertex, onHover, onHoverEnd, onClick } = props;
    const vertextId = vertex.id.toString();

    const classes = useStyles();

    const iconConversionX = 1.4;
    const iconConversionY = 1.5;

    const renderTooltipIcon = () => {
        if (vertex.data.exitId) 
            return <ExitToAppIcon className={classes.icon} />;

        // Render icons for special encounters
        switch(vertex.data.encounterId) {
            case "home":
                return <HomeIcon className={classes.icon} />;
            default:
                return <GpsFixedIcon className={classes.icon} />;
        }
    }

    const icon = new Leaflet.Icon({
        iconUrl: vertex.data.marker ? `/images/markers/${vertex.data.marker.image}.png` : "images/blank.svg",
        iconSize: vertex.data.marker
            ? [vertex.data.marker.dimensions[0] * iconConversionX, vertex.data.marker.dimensions[1] * iconConversionY]
            : [50, 50],
        iconAnchor: vertex.data.marker
            ? [vertex.data.marker.anchor[0] * iconConversionX, vertex.data.marker.anchor[1] * iconConversionY]
            : [25, 25],
        className: vertex.data.marker ? "vertex-icon" : vertex.data.exitId ? "exit-icon" : "bullseye-icon",
    });

    // Note: Leaflet tooltips breaks the onclick detection on the map object
    return (
        <Marker
            key={vertextId}
            position={vertex.data.position}
            icon={icon}
            onclick={() => onClick(vertextId)}
            onmouseover={() => onHover(vertextId)}
            onmouseout={() => onHoverEnd(vertextId)}
        >
            <Tooltip className={classes.tooltip}>
                {renderTooltipIcon()}
                <span>{vertex.id.toString()}</span>
            </Tooltip>
        </Marker>
    );
};

export default VertexMarker;
