import React from "react";
import { makeStyles, List, ListItem, ListItemAvatar, Avatar, ListItemText, Grid } from "@material-ui/core";

import HeroTrait from "../models/HeroTrait";

import FlashOnIcon from "@material-ui/icons/FlashOn";
import FaceIcon from "@material-ui/icons/Face";
import AccessibilityNewIcon from "@material-ui/icons/AccessibilityNew";
import AllInboxIcon from "@material-ui/icons/AllInbox";

const useStyles = makeStyles({
    statisticsBlock: {
        padding: "40px 0",
    },
});

type TraitListProps = {
    traits: HeroTrait[];
};

const TraitList: React.FunctionComponent<TraitListProps> = (props: TraitListProps) => {
    const classes = useStyles();

    const { traits } = props;

    const renderTraitIcon = ((t: HeroTrait) => {
        switch (t.type) {
            case 1: // Mental
                return <FaceIcon />;
            case 2: // Mystical
                return <FlashOnIcon />;
            case 3: // Physical
                return <AccessibilityNewIcon />;
            case 4: // Item
                return <AllInboxIcon />;
            default:
                return <AllInboxIcon />;
        }
    });


    // TODO: Add behiavor , {t.behaviour}
    const renderTrait = (t: HeroTrait) => (
        <ListItem key={t.id}>
            <ListItemAvatar>
                <Avatar>
                    {renderTraitIcon(t)}
                </Avatar>
            </ListItemAvatar>
            <ListItemText
                primary={t.name}
                secondary={t.from}
            />
        </ListItem>
    );

    return (
        <Grid container className={classes.statisticsBlock}>
            <Grid item xs={2}></Grid>
            <Grid item xs={8}>
                <List>{traits.map((t) => renderTrait(t))}</List>
            </Grid>
            <Grid item xs={2}></Grid>
        </Grid>
    );
}

export default TraitList;
