import React from "react";

import { makeStyles } from "@material-ui/core";
import { Icon, LatLng } from "leaflet";

import DriftMarker from "./DriftMarker";

const useStyles = makeStyles({
    icon: {
        borderRadius: "50%",
        border: "6px solid gold",
    },
});

/*
* Marker to represent a hero
*/
type HeroMarkerProps = {
    name: string;
    position: LatLng;
    onMoveEnd: (name: string) => void;
};

const HeroMarker: React.FunctionComponent<HeroMarkerProps> = (props: HeroMarkerProps) => {

    const { name, position, onMoveEnd } = props;

    const classes = useStyles();

    var markerIcon = new Icon({
        iconUrl: "images/knight.jpg",
        iconSize: [50, 50],
        iconAnchor: [25, 40],
        className: classes.icon,
        // popupAnchor: [-3, -76],
        // shadowUrl: 'my-icon-shadow.png',
        // shadowSize: [68, 95],
        // shadowAnchor: [22, 94]
    });

    return (
        <DriftMarker duration={1000} position={position} keepAtCenter={false} icon={markerIcon} onMoveEnd={(_: any) => onMoveEnd(name)} />
    );
};

export default HeroMarker;
