import React from "react";
import { makeStyles, Typography } from "@material-ui/core";

import Item from "../models/Item";
import Trait, { TraitEffect } from "../models/Trait";
import StringConverters from "../services/StringConverters";

const useStyles = makeStyles({
    title: {
        padding: "5px",
    },
    description: {
        padding: "10px",
        margin: "10px 0",
        backgroundColor: "#999",
    },
    itemType: {
        padding: "500px 10px 0 10px",
        margin: "100px 0 0 0 ",
        width: "100px",
        display: "inline",
    },
    traits: {
        padding: "0",
        margin: "0",
        width: "100px",
        display: "inline",
    },
});

type ItemTooltipContentProps = {
    item: Item | undefined;
};

const ItemTooltipContent: React.FunctionComponent<ItemTooltipContentProps> = (props: ItemTooltipContentProps): JSX.Element => {
    const classes = useStyles();
    const { item } = props;

    const renderTraits = (traits: Trait[]): JSX.Element => {
        let traitJsx: JSX.Element[] = [];
        for (let i = 0; i < traits.length; i++) {
            const newTraits = renderBehaviors(traits[i].behaviour);
            traitJsx = traitJsx.concat(newTraits);
        }
        return <div>{traitJsx}</div>;
    };

    const renderBehaviors = (effects: TraitEffect[]): JSX.Element[] => {
        return effects.map((e) => (
            <p>
                {valueToString(e.modifierValue)} {StringConverters.modifierToString(e.modifierType)}
            </p>
        ));
    };

    const valueToString = (value: number): string => (value > 0 ? `+${value}` : value.toString());

    if (!item) return <p>"None"</p>;

    return (
        <React.Fragment>
            <Typography className={classes.title} color="inherit">
                {item.name}
            </Typography>
            <p className={classes.description}>{item.description}</p>
            <Typography className={classes.itemType}>{StringConverters.itemTypeText(item.itemType)}</Typography>
            <Typography className={classes.traits} align="right">
                {renderTraits(item.traits)}
            </Typography>
        </React.Fragment>
    );
};

export default ItemTooltipContent;
