import React from 'react';
import { useHistory } from "react-router-dom";
import { Card, CardHeader, CardContent, Grid, makeStyles, Typography } from "@material-ui/core";

import LinkTo from '../components/LinkTo';
import KeyMaps from '../components/KeyMaps';

const useStyles = makeStyles({
    root: {
        backgroundColor: "#282c34",
        height: "100vh",
    },
    close: {
        filter: "invert(60%)",
    },
    container: {
        top: "8vh",
        position: "relative",
    },
    titleContainer: {
        padding: "10vh",
    },
    appLogo: {
        height: "10vh",
        pointerEvents: "none",
        padding: "20px",
    },
    card: {
        position: "relative",
        margin: "40px",
        cursor: "pointer",
        backgroundColor: "lightsteelblue",
        "&:hover": {
            top: "-5px",
            right: "5px",
            boxShadow: "10px 10px",
            backgroundColor: "ghostwhite",
        },
    },
});

const MainMenu: React.FunctionComponent = () => {

    const classes = useStyles();

    let history = useHistory();
    const navigateToSaves = () => history.push("/saves");

    const isElectron = () => {
        // Renderer process
        if (typeof window !== "undefined" && typeof window.process === "object" && window.process.type === "renderer") {
            return true;
        }

        // Main process
        if (typeof process !== "undefined" && typeof process.versions === "object" && !!process.versions.electron) {
            return true;
        }

        // Detect the user agent when the `nodeIntegration` option is set to true
        if (typeof navigator === "object" && typeof navigator.userAgent === "string" && navigator.userAgent.indexOf("Electron") >= 0) {
            return true;
        }

        return false;
    }

    const onElectronExit = () => {
        const ipcRenderer = window.require("electron").ipcRenderer;
        ipcRenderer.send("close-app");
    }
    
    return (
        <KeyMaps backUrl="/">
            <div className={classes.root}>
                <LinkTo url={`${process.env.PUBLIC_URL}/`} top="20px" left="20px" width="40px">
                    <img src="images/close.svg" className={classes.close} alt="close" />
                </LinkTo>
                <div className={classes.titleContainer}>
                    <img src="./volcano.svg" className={classes.appLogo} alt="logo" />
                    <img className="title-large shadow" alt="Tales from the Pit" src="title.svg" />
                </div>
                <Grid container className={classes.container}>
                    <Grid item xs={3}></Grid>
                    <Grid item xs={6}>
                        <Card className={classes.card}>
                            <CardHeader
                                title={
                                    <Typography variant="h2" onClick={navigateToSaves}>
                                        New Game
                                    </Typography>
                                }
                            />
                            <CardContent>Start a new game</CardContent>
                        </Card>
                        {isElectron() ? (
                            <Card className={classes.card} onClick={onElectronExit}>
                                <CardHeader title={<Typography variant="h2">Quit</Typography>} />
                                <CardContent>Exit the game</CardContent>
                            </Card>
                        ) : null}
                    </Grid>
                    <Grid item xs={3}></Grid>
                </Grid>
            </div>
        </KeyMaps>
    );
}

export default MainMenu;
