import React from "react";
import { useHistory } from "react-router-dom";
import Mousetrap from "mousetrap";

type KeyMapsProps = {
    backUrl: string;
    children: React.ReactNode;
};

const KeyMaps: React.FunctionComponent<KeyMapsProps> = (props: KeyMapsProps) => {
    const { backUrl, children } = props;

    const history = useHistory();
    const navigateToUrl = (url: string): void => history.push(url);

    Mousetrap.bind("1", () => navigateToUrl("/profile/1"));
    Mousetrap.bind("2", () => navigateToUrl("/profile/2"));
    Mousetrap.bind("3", () => navigateToUrl("/profile/3"));
    Mousetrap.bind("4", () => navigateToUrl("/profile/4"));
    Mousetrap.bind("esc", () => navigateToUrl(backUrl));

    return <div>{children}</div>;
};

export default KeyMaps;
