import Trait from "./Trait";

export enum ItemType {
    Usable = 1,
    Quest = 2,
    WearableHead = 10,
    WearableBody = 11,
    WearableArms = 12,
    WearableFeet = 13,
    WeaponMelee1h = 20,
    WeaponMelee2h = 21,
    WeaponRanged1h = 22,
    WeaponRanged2h = 23,
    Shield = 24,
}

export default interface Item {
    // ID of the item
    id: string;

    // Name of the item
    name: string;

    // Text description for the item
    description: string;

    // URL of the image to display for the item
    imageUrl: string;

    // The type of item
    itemType: ItemType;

    // Item traits
    traits: Trait[];
}