import React from "react";
import { makeStyles, Tooltip } from "@material-ui/core";
import Item, { ItemType } from "../models/Item";
import ItemTooltipContent from "./ItemTooltipContent";

const useStyles = makeStyles({
    itemContainer: {
        position: "relative",
    },
    portraitItem: {
        position: "absolute",
        width: "5.5vh",
        height: "5.5vh",
        border: "solid",
        borderRadius: "50%",
        borderWidth: "6px",
        borderColor: "lightslategrey",
    },
    portraitItemHelm: {
        left: "7vw",
        top: "-1vh",
    },
    portraitItemBreastplate: {
        left: "7vw",
        top: "6.5vh",
    },
    portraitItemGloves: {
        left: "7vw",
        top: "14vh",
    },
    portraitItemLegs: {
        left: "7vw",
        top: "21.5vh",
    },
    portraitItemWeapon: {
        left: "3.5vw",
        top: "28vh",
    },
    portraitItemShield: {
        left: "10.5vw",
        top: "28vh",
    },
    portraitNoItemImg: {
        filter: "brightness(35%)",
    },
    portraitItemImg: {
        width: "5.5vh",
        height: "5.5vh",
        backgroundColor: "lightblue",
        borderRadius: "50%",
    },
});

type EquippedItemsProps = {
    equippedItems: Item[];
};

/*
* Hero Profile Screen : Display the items that the hero has equipped
*/
const EquippedItems: React.FunctionComponent<EquippedItemsProps> = (props: EquippedItemsProps): JSX.Element => {
    const classes = useStyles();

    const { equippedItems } = props;

    const headItem = equippedItems.find((item) => item.itemType === ItemType.WearableHead);
    const bodyItem = equippedItems.find((item) => item.itemType === ItemType.WearableBody);
    const armsItem = equippedItems.find((item) => item.itemType === ItemType.WearableArms);
    const feetItem = equippedItems.find((item) => item.itemType === ItemType.WearableFeet);

    return (
        <div className={classes.itemContainer}>
            <Tooltip title={<ItemTooltipContent item={headItem} />} arrow>
                <div className={`${classes.portraitItem} ${classes.portraitItemHelm}`}>
                    {headItem ? (
                        <img src={headItem.imageUrl} className={classes.portraitItemImg} alt="helm" />
                    ) : (
                        <img src="images/helm.svg" className={classes.portraitNoItemImg} alt="helm" />
                    )}
                </div>
            </Tooltip>
            <Tooltip title={<ItemTooltipContent item={bodyItem} />} arrow>
                <div className={`${classes.portraitItem} ${classes.portraitItemBreastplate}`}>
                    {bodyItem ? (
                        <img src={bodyItem.imageUrl} className={classes.portraitItemImg} alt="breastplate" />
                    ) : (
                        <img src="images/breastplate.svg" className={classes.portraitNoItemImg} alt="breastplate" />
                    )}
                </div>
            </Tooltip>
            <Tooltip title={<ItemTooltipContent item={feetItem} />} arrow>
                <div className={`${classes.portraitItem} ${classes.portraitItemLegs}`}>
                    {feetItem ? (
                        <img src={feetItem.imageUrl} className={classes.portraitItemImg} alt="legs" />
                    ) : (
                        <img src="images/leg.svg" className={classes.portraitNoItemImg} alt="legs" />
                    )}
                </div>
            </Tooltip>
            <Tooltip title={<ItemTooltipContent item={armsItem} />} arrow>
                <div className={`${classes.portraitItem} ${classes.portraitItemGloves}`}>
                    {armsItem ? (
                        <img src={armsItem.imageUrl} className={classes.portraitItemImg} alt="gloves" />
                    ) : (
                        <img src="images/glove.svg" className={classes.portraitNoItemImg} alt="gloves" />
                    )}
                </div>
            </Tooltip>
            <Tooltip title={"None"} arrow>
                <div className={`${classes.portraitItem} ${classes.portraitItemWeapon}`}>
                    <img src="images/sword.svg" className={classes.portraitNoItemImg} alt="weapon" />
                </div>
            </Tooltip>
            <Tooltip title={"None"} arrow>
                <div className={`${classes.portraitItem} ${classes.portraitItemShield}`}>
                    <img src="images/shield.svg" className={classes.portraitNoItemImg} alt="shield" />
                </div>
            </Tooltip>
        </div>
    );
};

export default EquippedItems;
