import React from "react";
import { useHistory } from "react-router-dom";
import { makeStyles } from "@material-ui/core";

type LinkToProps = {
    url: string;
    children?: React.ReactNode;
    top?: string,
    left?: string;
    width?: string;
};

const LinkTo: React.FunctionComponent<LinkToProps> = (props: LinkToProps) => {
    
    const { url, children, top, left, width } = props;

    const useStyles = makeStyles({
        container: {
            zIndex: 2,
            display: "inline",
            cursor: "pointer",
        },
        containerAbsolute: {
            position: "absolute",
            top: top ?? "20px",
            left: left ?? "20px",
            width: width ?? "40px",
            zIndex: 2,
            display: "inline",
            cursor: "pointer",
        },
    });

    const classes = useStyles();
    
    const isPositioned = top || left || width;

    const history = useHistory();
    const navigateToUrl = (event: React.MouseEvent<HTMLDivElement, MouseEvent>): void => history.push(url);

    return (
        <div className={isPositioned ? classes.containerAbsolute : classes.container} onClick={navigateToUrl}>
            {children}
        </div>
    );
};

export default LinkTo;
